.fibonacci-container {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(135deg, #f39c12 0%, #f1c40f 100%);
  padding: 0;
  margin: 0;
}

.fibonacci-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
  margin-bottom: 20px;
}

.header-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 2px solid #e0e0e0;
}

.header-section h2 {
  margin: 0;
  color: #2c3e50;
  font-size: 1.8rem;
  font-weight: 600;
}

.compact-input-group {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}

.compact-input {
  width: 80px;
  padding: 8px 12px;
  border: 2px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
  background: white;
  transition: border-color 0.2s ease;
}

.compact-input:focus {
  outline: none;
  border-color: #f39c12;
}

.compact-slider {
  width: 100px;
  height: 6px;
  background: #ddd;
  border-radius: 3px;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
}

.compact-slider:hover {
  opacity: 1;
}

.compact-slider::-webkit-slider-thumb {
  appearance: none;
  width: 16px;
  height: 16px;
  background: #f39c12;
  border-radius: 50%;
  cursor: pointer;
}

.compact-slider::-moz-range-thumb {
  width: 16px;
  height: 16px;
  background: #f39c12;
  border-radius: 50%;
  cursor: pointer;
  border: none;
}

.angle-value {
  font-family: monospace;
  font-weight: bold;
  color: #f39c12;
  min-width: 50px;
  font-size: 13px;
}

.compact-checkbox {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 14px;
  color: #555;
  cursor: pointer;
}

.compact-checkbox input[type="checkbox"] {
  margin: 0;
  cursor: pointer;
}

.visualization-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 20px;
  margin-bottom: 20px;
}

.chart-section {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 15px;
  border: 2px solid #ddd;
}

.chart-section h3 {
  margin: 0 0 15px 0;
  color: #2c3e50;
  font-size: 1.2rem;
  text-align: center;
}

.matrix-section {
  background: #fff3cd;
  border-left: 4px solid #f39c12;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 0 8px 8px 0;
}

.matrix-section h3 {
  margin: 0 0 15px 0;
  color: #2c3e50;
  font-size: 1.2rem;
}

.matrix-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
}

.matrix-item {
  background: white;
  padding: 15px;
  border-radius: 6px;
  border: 1px solid #f39c12;
  text-align: center;
}

.matrix-power {
  font-weight: bold;
  color: #2c3e50;
  margin-bottom: 8px;
  font-family: 'Courier New', monospace;
}

.matrix-result {
  font-family: 'Courier New', monospace;
  color: #7f8c8d;
  margin-bottom: 8px;
  font-size: 0.9em;
}

.matrix-fib {
  color: #f39c12;
  font-weight: bold;
}

.fibonacci-description {
  background: #e8f4f8;
  border-left: 4px solid #3498db;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 0 8px 8px 0;
}

.fibonacci-description p {
  margin: 0;
  line-height: 1.6;
  color: #34495e;
}

.fibonacci-info {
  background: #f8f9fa;
  padding: 20px;
  border-radius: 8px;
  border-left: 4px solid #f39c12;
}

.fibonacci-info p {
  margin: 0 0 15px 0;
  line-height: 1.6;
  color: #34495e;
}

.fibonacci-info ul {
  margin: 15px 0;
  padding-left: 20px;
}

.fibonacci-info li {
  margin-bottom: 8px;
  line-height: 1.5;
  color: #34495e;
}

.fibonacci-info strong {
  color: #2c3e50;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .fibonacci-content {
    margin: 10px;
    padding: 15px;
  }
  
  .header-section {
    flex-direction: column;
    gap: 15px;
    align-items: stretch;
  }
  
  .header-section h2 {
    text-align: center;
    font-size: 1.5rem;
  }
  
  .compact-input-group {
    justify-content: center;
  }
  
  .visualization-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .chart-section {
    padding: 10px;
  }
  
  .matrix-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 10px;
  }
}

/* iPhone specific optimizations */
@media (max-width: 480px) {
  .fibonacci-content {
    margin: 5px;
    padding: 10px;
  }
  
  .compact-input {
    width: 70px;
    padding: 6px 8px;
    font-size: 13px;
  }
  
  .compact-slider {
    width: 80px;
  }
  
  .compact-checkbox {
    font-size: 13px;
  }
  
  .angle-value {
    font-size: 12px;
    min-width: 45px;
  }
  
  .matrix-item {
    padding: 10px;
  }
  
  .matrix-power, .matrix-result, .matrix-fib {
    font-size: 0.8em;
  }
}
